<template>
  <article class="set-password">
    <back-head>
      <span class="back-content"></span>
    </back-head>
    <section class="set-password-content">
      <div class="tips">
        <p>238****234</p>
        <span>6</span>
      </div>
      <div class="set-password-content-password-wrapper" ref="codeWrapper">
        <input class="mui-numbox-input" maxlength="2" @input="watchInp($event)" @keyup.delete='deleteCode($event)' />
        <input class="mui-numbox-input" maxlength="2" @input="watchInp($event)" @keyup.delete='deleteCode($event)' />
        <input class="mui-numbox-input" maxlength="2" @input="watchInp($event)" @keyup.delete='deleteCode($event)' />
        <input class="mui-numbox-input" maxlength="2" @input="watchInp($event)" @keyup.delete='deleteCode($event)' />
        <input class="mui-numbox-input" maxlength="2" @input="watchInp($event)" @keyup.delete='deleteCode($event)' />
        <input class="mui-numbox-input" maxlength="2" @input="watchInp($event)" @keyup.delete='deleteCode($event)' />
      </div>
      <p></p>
    </section>
  </article>
</template>
<script>
import BackHead from 'commonComponent/head/BackHead'
import FootNext from 'commonComponent/footNext/FootNext'
export default {
  components: {
    BackHead,
    FootNext
  },
  data () {
    return {
      verificationCode: []
    }
  },
  methods: {
    watchInp (event) {
      var e = event || window.event
      var target = e.target || e.srcElement
      var inps = this.$refs.codeWrapper.children
      var isNumber = /[\d]/.test(target.value)
      if (isNumber) {
        for (var i = 0; i < inps.length; i++) {
          if (target === inps[i]) {
            if (this.verificationCode['num' + i] === 'done') {
              target.value = target.value[1]
            }
            this.$set(this.verificationCode, i, target.value)
            this.verificationCode['num' + i] = 'done'
            if (
              i < inps.length - 1 &&
              this.verificationCode['num' + (i + 1)] !== 'done'
            ) {
              // 
              inps[i + 1].focus()
              this.verificationCode['num' + (i + 1)] = 'will'
            }
            // 
            var codeStr = this.verificationCode.join('')
            if (codeStr.length === 6) {
              // 
              this.verifyCode(codeStr)
            }
          }
        }
      } else {
        target.value = ''
        // event.preventDefault()
      }
    },
    deleteCode (event) {
      var e = event || window.event
      var target = e.target || e.srcElement
      var inps = this.$refs.codeWrapper.children
      for (var i = 0; i < inps.length; i++) {
        if (target === inps[i] && i >= 0) {
          // ['num'+i] 
          // will
          // empty: 
          // clear: 
          // done: 
          // ['num'+i]empty
          // empty 
          // ['num'+i]'claer'
          // ()
          this.$set(this.verificationCode, i, target.value)
          if (
            this.verificationCode['num' + i] === 'empty' ||
            this.verificationCode['num' + i] === 'will'
          ) {
            if (i !== 0) {
              inps[i - 1].focus()
            }
            this.verificationCode['num' + i] = 'cealr'
          } else {
            this.verificationCode['num' + i] = 'empty'
          }
        }
      }
    },
    verifyCode (code) {
      var start = this.verificationCode[0] - 0
      var end = this.verificationCode[5] - 0
      var isRepeat =
        this.unique(this.verificationCode).length === 1 ? 'true' : 'false'
      if (end - start === 5) {
        this.$mui.toast('', { type: 'div' })
        document.getElementsByClassName('mui-toast-container')[0].className +=
          ' message-middle'
      } else if (isRepeat === 'true') {
        this.$mui.toast('', { type: 'div' })
        document.getElementsByClassName('mui-toast-container')[0].className +=
          ' message-middle'
      } else {
        this.$router.push({ name: 'SetPasswordConfirm', params: {password: this.verificationCode} })
      }
    },
    unique (arr) {
      // 6
      var tmp = {}
      var result = []
      var len = arr.length
      for (let i = 0; i < len; i++) {
        if (!tmp[arr[i]]) {
          tmp[arr[i]] = 'c'
          result.push(arr[i])
        }
      }
      return result
    }
  }
}
</script>
<style lang="scss" scoped>
@import '~styles/common.scss';
.set-password {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: #f7f7f7;
  overflow: hidden;
}
.set-password-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  & > p {
    align-self: center;
    color: #9c9c9c;
  }
  .tips {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 0.7rem;
    color: #000;
    p {
      font-size: 0.32rem;
      color: #000;
    }
  }
  .set-password-content-password-wrapper {
    width: 100%;
    padding: 0;
    display: flex;
    justify-content: center;
    margin-top: 0.38rem;
    margin-bottom: 0.46rem;
    & > input {
      box-sizing: border-box;
      width: 0.74rem;
      height: 0.74rem;
      border-radius: 0;
      text-align: center;
      border: 1px solid #b3b3b3;
      border-right: none;
      &:last-child {
        border-right: 1px solid #b3b3b3;
      }
    }
  }
}
</style>



