import AlertComponent from './alert.vue'
import Vue from 'vue'
const AlertConstructor = Vue.extend(AlertComponent)
const instance = new AlertConstructor() //  alert 
const defaultConfig = {
  title: '',
  content: '',
  hasBtn: true
}
const Alert = {
  install: function (Vue) {
    instance.config = defaultConfig
    instance.$mount(document.createElement('div')) // DOM
    Vue.nextTick(() => {
      document.body.appendChild(instance.$el)
    })
    Vue.prototype.$alert = (config) => {
      merge(config)
      instance.show = true //  $alert 
    }
  }
}
function merge (config) {
  instance.config = {
    title: '',
    content: '',
    hasBtn: true,
    isUseExplain: false
  }
  if (typeof config === 'object') {
    for (var prop in config) {
      if (config.hasOwnProperty(prop)) {
        instance.config[prop] = config[prop]
      }
    }
  }
}
export default Alert



